.catalog {
  display: flex;
  flex-wrap: wrap;
  max-width: 944px;

  &__card {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 200px;
    height: 300px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-bottom: 48px;
    transition: transform 0.3s, color 0.3s;

    &:hover {
      transform: scale(1.2);
      color: #34568b;
    }

    &:not(:nth-child(4n)) {
      margin-right: 48px;
    }
  }
}
